html, body {
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

body {
  background: #222;
  flex-direction: row;
  display: flex;
  overflow: hidden;
}

#editor {
  width: 50%;
  height: 100%;
  overflow: hidden;
}

#output {
  box-sizing: border-box;
  background: none;
  width: 50%;
  height: 100%;
  display: block;
  overflow-y: scroll;
}

#output .output__list {
  aspect-ratio: 1;
  box-sizing: border-box;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-auto-flow: row;
  width: 100%;
  height: 100%;
  display: grid;
}

#output .output__list .output__list__item {
  text-shadow: 0 0 1px #555;
  box-sizing: border-box;
  border: 1px solid #000;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 6px;
  width: 100%;
  height: 100%;
  display: flex;
}

#output .output__list .output__list__item.output__list__item--light {
  color: #232323;
}

#output .output__list .output__list__item.output__list__item--dark {
  color: #efefef;
}

#output .output__list .output__list__item > div {
  user-select: all;
  text-align: center;
}

#output .output__list .output__list__item > div:first-child {
  margin-bottom: 10px;
  padding-bottom: 10px;
}

#output .output__list .output__list__item > div:not(:first-child) {
  font-size: .75rem;
  font-style: italic;
}

#output .output__list .output__list__item.output__list__item--light > div:first-child {
  border-bottom: 1px solid #232323;
}

#output .output__list .output__list__item.output__list__item--dark > div:first-child {
  border-bottom: 1px solid #efefef;
}
